import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Home from "./views/home";
import Booking from "./views/bookings";
import Review from "./views/reviews";
import Nav from "./componets/nav/nav";
import OrderManger from "./views/OrderManger";
import "./app.css";

function App() {
  return (
    <div className={"content"}>
      <Router>
        <Nav />
        <Switch>
          <Route path="/manage">
            <OrderManger />
          </Route>
          <Route path="/reviews">
            <Review />
          </Route>
          <Route path="/bookings">
            <Booking />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
