import React, { useState, useEffect } from "react";
import "./view.css";
import Form from "../componets/form/BookingForm";
import OrderHolder from "../componets/orders/OrdersHolder";
import "./booking.css";
import { getBookings } from "../api/bookings";

export default function Booking() {
  const [loading, setStateLoading] = useState(true);
  const [error, setErrorLoading] = useState(null);
  const [bookings, setBookings] = useState(null);

  const getBookingsInformation = async () => {
    try {
      setStateLoading(true);
      const bookings = await getBookings();
      setBookings(bookings as any);
    } catch (e) {
      setErrorLoading(e);
    } finally {
      setStateLoading(false);
    }
  };

  useEffect(() => {
    getBookingsInformation();
  }, []);

  if (error)
    return (
      <div className={"view"}>
        <p>Failed to load resource for bookings</p>;{" "}
      </div>
    );
  return loading ? (
    <div className={"view"}>
      <p>loading...</p>
    </div>
  ) : (
    <div className={"view"}>
      <h3 className={"bannerLogo"}> Chloe Chart - Tarot and Oracle Readings</h3>
      <p className={"title"}>bookings</p>
      <Form></Form>
      <OrderHolder bookings={bookings}> </OrderHolder>
    </div>
  );
}
