import React from "react";
import "./form.css";
import { createBooking } from "../../api/bookings";

export default function bookForm() {
  const formsObject = {} as any;

  const handleInputChange = (event: any) => {
    const id = event.target.id;
    console.log("id", event.target.value);
    formsObject[id] = event.target;
  };
  const submitForm = (event: any) => {
    event.preventDefault();
    const name = formsObject["name"].value;
    const request = formsObject["request"].value;
    const email = formsObject["email"].value;
    createBooking({ name, request, email });
  };

  return (
    <div className={"formHolder"}>
      <form onSubmit={submitForm} className={"form"}>
        <div className={"inputGroup"}>
          <label>Name</label>
          <input id={"name"} onChange={handleInputChange} type={"text"} />
        </div>
        <div className={"inputGroup"}>
          <label>Booking Request</label>
          <input id={"request"} onChange={handleInputChange} type={"text"} />
        </div>
        <div className={"inputGroup"}>
          <label>Email</label>
          <input id={"email"} onChange={handleInputChange} type={"email"} />
        </div>
        <div className={"inputGroup"}>
          <input type={"submit"} />
        </div>
      </form>
    </div>
  );
}
