import React, { useEffect, useState } from "react";
import "./view.css";
import { getBookings, updateBooking } from "../api/bookings";

export default function OrderManger() {
  const [loading, setStateLoading] = useState(true);
  const [error, setErrorLoading] = useState(null);
  const [bookings, setBookings] = useState({
    bookings: [],
  });

  const getBookingsInfo = async () => {
    try {
      setStateLoading(true);
      const bookings = await getBookings();
      setBookings(bookings as any);
    } catch (e) {
      setErrorLoading(e);
    } finally {
      setStateLoading(false);
    }
  };

  const setInProgress = async (id: string, password: string) => {
    updateBooking(id, "inProgress", password);
    window.location.reload();
  };

  const setComplete = async (id: string, password: string) => {
    updateBooking(id, "Completed", password);
    window.location.reload();
  };

  const Booking = (index: number, order: any, key: any, password: string) => {
    if (index % 2) {
      return (
        <div key={key} className={"ordersOdd"}>
          <p>Order ID: {`${order.bookingId}`}</p>
          <p>Name: {`${order.name}`}</p>
          <p>State: {`${order.state}`}</p>
          <button
            id={"progress"}
            onClick={() => {
              setInProgress(order.bookingId, password);
            }}
          >
            In Progress
          </button>
          <button
            id={"complete"}
            onClick={() => {
              setComplete(order.bookingId, password);
            }}
          >
            Complete
          </button>
        </div>
      );
    } else {
      return (
        <div key={key} className={"ordersEven"}>
          <p>Order ID: {`${order.bookingId}`}</p>
          <p>Name: {`${order.name}`}</p>
          <p>State: {`${order.state}`}</p>
          <button
            id={"progress"}
            onClick={() => {
              setInProgress(order.bookingId, password);
            }}
          >
            In Progress
          </button>
          <button
            id={"complete"}
            onClick={() => {
              setComplete(order.bookingId, password);
            }}
          >
            Complete
          </button>
        </div>
      );
    }
  };

  function setCookie(name: string, value?: string | null, days?: number) {
    let expires = "";
    if (days) {
      let date = new Date();
      date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
      expires = "; expires=" + date.toUTCString();
    }
    document.cookie = name + "=" + (value || "") + expires + "; path=/";
  }

  const awaitPasswordPrompt = () => {
    return new Promise((resolve) => {
      if (!getCookie("pwd")) {
        const psword = prompt("Enter Password");
        setCookie("pwd", psword, 1);
        resolve("");
      }
    });
  };

  function getCookie(name: string) {
    let nameEQ = name + "=";
    let ca = document.cookie.split(";");
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) === " ") c = c.substring(1, c.length);
      if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length, c.length);
    }
    return "";
  }

  useEffect(() => {
    // @ts-ignore
    awaitPasswordPrompt().then(getBookingsInfo());
  }, []);

  const passwordCookie = getCookie("pwd");

  if (error)
    return (
      <div className={"view"}>
        <p>Failed to load resource for bookings</p>;{" "}
      </div>
    );

  return loading ? (
    <div className={"view"}>
      <p>loading...</p>
    </div>
  ) : (
    <div className={"view"}>
      <h3 className={"bannerLogo"}> Chloe Chart - Tarot and Oracle Readings</h3>
      {bookings.bookings.map((e, i) => {
        return Booking(i, e, i, passwordCookie);
      })}
    </div>
  );
}
