import { ApolloClient, InMemoryCache, gql } from "@apollo/client";
// @ts-ignore
import { v4 as uuid } from "uuid";

export function getBookings() {
  const client = new ApolloClient({
    uri: "/graphql",
    cache: new InMemoryCache(),
  });

  return client
    .query({
      query: gql`
        query {
          allBookings {
            bookingId
            name
            email
            request
            state
          }
        }
      `,
    })
    .then((result) => {
      const { data: allBookings } = result;
      return { bookings: allBookings.allBookings };
    });
}

export function updateBooking(id: string, state: string, password: string) {
  const client = new ApolloClient({
    uri: "/graphql",
    cache: new InMemoryCache(),
    headers: {
      password: password,
    },
  });

  const update = `
  mutation UpdateBooking {
     updateBookingOne(filter: {bookingId: "${id}"} record: { state: "${state}"})  {
        record {
            state
        }

    }
}`;

  client.mutate({
    mutation: gql`
      ${update}
    `,
  });
}

type bookings = {
  name: string;
  email: string;
  request: string;
};

export async function createBooking({ name, email, request }: bookings) {
  const id = uuid();

  const mutation = `
        mutation CreateBooking {
          bookingCreateOne(
            record: {
              bookingId: "${id}"
              name: "${name}"
              email: "${email}"
              request: "${request}"
              state: "placed"
            }
          ) {
            record {
              bookingId
              name
              email
              request
              state
            }
          }
        }
      `;
  console.log(mutation);
  const opts = {
    method: "POST",
    mode: "cors",
    body: JSON.stringify({ mutation }),
    headers: {
      "Content-Type": "application/json",
    },
  };
  // @ts-ignore
  await fetch("/kafka", opts);
}
