import React from "react";
import Orders from "./orders";

export default function OrdersHolder({ bookings }: any) {
  return (
    <div className={"orders_holdrs"}>
      {bookings.bookings.map((e: any, i: any) => {
        return <Orders order={e} index={i} key={i} />;
      })}
    </div>
  );
}
