// @ts-ignore
import { v4 as uuid } from "uuid";
import { ApolloClient, InMemoryCache, gql } from "@apollo/client";

const client = new ApolloClient({
  uri: "/graphql",
  cache: new InMemoryCache(),
});

export function getReviews() {
  // @ts-ignore
  return client
    .query({
      query: gql`
        query {
          allReviews {
            reviewId
            name
            review
          }
        }
      `,
    })
    .then((result: { data: any }) => {
      const { data: allReviews } = result;
      return { reviews: allReviews.allReviews };
    });
}

export function createReview(name?: string, review?: string, request?: string) {
  const id = uuid();

  const mutation = `
      mutation CreateReview {
    ReviewsCreateOne(record: {name: "${name}",review : "${review}", reviewId:"${id}"})  {
        record {
           name
           review
           reviewId
        }
    }
}
      `;

  client.mutate({
    mutation: gql`
      ${mutation}
    `,
  });
}
