import React from "react";
import "./review.css";

export default function Review({ review, index }: any) {
  if (index % 2) {
    return (
      <div className={"ordersOdd"}>
        <p>Review ID: {`${review.reviewId}`}</p>
        <p>Name: {`${review.name}`}</p>
        <p>Review: {`${review.review}`}</p>
      </div>
    );
  } else {
    return (
      <div className={"ordersEven"}>
        <p>Review ID: {`${review.reviewId}`}</p>
        <p>Name: {`${review.name}`}</p>
        <p>Review: {`${review.review}`}</p>
      </div>
    );
  }
}
