import React from "react";
import "./orders.css";

export default function Orders({ order, index }: any) {
  if (index % 2) {
    return (
      <div className={"ordersOdd"}>
        <p>Order ID: {`${order.bookingId}`}</p>
        <p>Name: {`${order.name}`}</p>
        <p>State: {`${order.state}`}</p>
      </div>
    );
  } else {
    return (
      <div className={"ordersEven"}>
        <p>Order ID: {`${order.bookingId}`}</p>
        <p>Name: {`${order.name}`}</p>
        <p>State: {`${order.state}`}</p>
      </div>
    );
  }
}
