import React, { useEffect, useState } from "react";
import "./view.css";
import ReviewHolder from "../componets/reviews/ReviewHolder";
import { getReviews } from "../api/review";
import ReviewForm from "../componets/form/ReviewForm";

export default function Reviews() {
  const [loading, setStateLoading] = useState(true);
  const [error, setErrorLoading] = useState(null);
  const [reviews, setReviews] = useState(null);

  const getReviewsAll = async () => {
    try {
      setStateLoading(true);
      const reviews = await getReviews();
      setReviews(reviews as any);
    } catch (e) {
      console.log(e);
      setErrorLoading(e);
    } finally {
      setStateLoading(false);
    }
  };

  useEffect(() => {
    getReviewsAll();
  }, []);

  if (error)
    return (
      <div className={"view"}>
        <p>Failed to load resource for reviews</p>;{" "}
      </div>
    );

  return loading ? (
    <div className={"view"}>
      <p>loading...</p>
    </div>
  ) : (
    <div className={"view"}>
      <h3 className={"bannerLogo"}> Chloe Chart - Tarot and Oracle Readings</h3>
      <p className={"title"}>Reviews</p>
      <ReviewForm></ReviewForm>
      <ReviewHolder reviews={reviews}></ReviewHolder>
    </div>
  );
}
